import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DynamicAPIInterface } from 'src/app/shared/models/dynamicAPI.interface';
import { DynamicAPIService } from 'src/app/shared/service/dynamicApi.service';
import { NotificationService } from 'src/app/shared/service/notification.service';
import { UsersService } from 'src/app/shared/service/users.service';

@Component({
  selector: 'app-dynamic-api-update',
  templateUrl: './dynamic-api-update.component.html',
  styleUrls: ['./dynamic-api-update.component.scss']
})
export class DynamicApiUpdateComponent implements OnInit {
  @ViewChild(FormGroupDirective) formGroupDirective: FormGroupDirective;
  dynamicAPIEditFG: FormGroup;
  hidden = false;
  localStorageCompany: any;
  dyanmicAPIData: DynamicAPIInterface;
  options = [
    { label: 'One Date', value: 'one' },
    { label: 'Two Date', value: 'two' },
  ];
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private notification: NotificationService,
    private userService: UsersService,
    private dynamicAPIService: DynamicAPIService,
  ) { 
    this.localStorageCompany = this.getSelectedCompanyFromLocalStorage();
  }

  ngOnInit(): void {
    if (!this.localStorageCompany) {
      this.hidden = true;
      this.notification.showError("Please select your Company!", "")
    } else {
      this.hidden = false;
    }

    this.dynamicAPIEditFG = new FormGroup({
      dynamicApiUri: new FormControl('', [Validators.required, Validators.pattern(/^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/)]),
      responseFields: new FormControl(''),
      selectedOption: new FormControl(''),
      subid: new FormControl(''),
      subidKey: new FormControl({ value: '', disabled: true }),
    });

    if (this.route.snapshot.params.id) {
      this.dynamicAPIService.getOneDynamicAPI(this.route.snapshot.params.id).subscribe(res => {
        this.dyanmicAPIData = res;

        this.dynamicAPIEditFG.setValue({
          dynamicApiUri: res['dynamicApiUri'],
          responseFields: res['responseFields'] !== 'never' ? res['responseFields'] : '',
          selectedOption: res['selectedOption'] !== 'never' ? res['selectedOption'] : '',
          subid: res['subid'],
          subidKey: res['subidKey'] !== 'never' ? res['subidKey'] : '',
        });

        const subidKeyControl = this.dynamicAPIEditFG.get('subidKey');
        if (res['subid']) {
          subidKeyControl.enable();
        } else {
          subidKeyControl.disable();
        }
      })
    }
  }

  //Gets the Selected Company from Local Storage
  getSelectedCompanyFromLocalStorage() {
    return this.userService.getSelectedCompanyFromLocalStorage();
  }

  onSubidChange(event: any) {
    const subidKeyControl = this.dynamicAPIEditFG.get('subidKey')
    if (event.value) {
      subidKeyControl.enable();
    } else {
      subidKeyControl.disable();
    }
  }

  submitHandle() {
    this.dynamicAPIEditFG.markAllAsTouched();
    if (this.dynamicAPIEditFG.valid) {
      this.dyanmicAPIData = { ...this.dyanmicAPIData, ...this.dynamicAPIEditFG.value };
      this.dynamicAPIService.updateDynamicAPI(this.dyanmicAPIData).subscribe(res => {
        this.notification.showSuccess('Successfully updated a new Dynamic API.', "");
        this.router.navigate(['/dynamicAPI/all-dynamic-api']);
      }, (error) => {
        this.notification.showError(JSON.stringify(error.error), "");
      })
    }
  }

}
