import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { DynamicAPIInterface } from '../models/dynamicAPI.interface';

const API_DYNAMICAPI_URL = `${environment.apiUrl}/dynamicAPI`;

@Injectable({
    providedIn: 'root',
})

export class DynamicAPIService {
    constructor(private http: HttpClient) { }

    createDynamicAPI(dynamicAPI: DynamicAPIInterface): Observable<DynamicAPIInterface> {
        return this.http.post<DynamicAPIInterface>(`${API_DYNAMICAPI_URL}/create`, dynamicAPI);
    }

    getDynamicAPIs(): Observable<DynamicAPIInterface[]> {
        return this.http.get<DynamicAPIInterface[]>(`${API_DYNAMICAPI_URL}/all-dynamicApis`);
    }

    getOneDynamicAPI(dynamicAPI: DynamicAPIInterface): Observable<DynamicAPIInterface> {
        return this.http.get<DynamicAPIInterface>(
            API_DYNAMICAPI_URL + `/get-dynamicApi/${dynamicAPI}`
        );
    }

    updateDynamicAPI(dynamicAPI: DynamicAPIInterface): Observable<DynamicAPIInterface> {
        return this.http.put<DynamicAPIInterface>(
            `${API_DYNAMICAPI_URL}/update/${dynamicAPI._key}`,
            dynamicAPI
        );
    }

    deleteDynamicAPI(dynamicAPI: string) {
        return this.http.delete(API_DYNAMICAPI_URL + `/delete/${dynamicAPI}`);
    }
}