import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AllDynamicAPIComponent } from './all-dynamic-api/all-dynamic-api.component';
import { DynamicApiCreateComponent } from './dynamic-api-create/dynamic-api-create.component';
import { DynamicApiUpdateComponent } from './dynamic-api-update/dynamic-api-update.component';
import { DynamicAPIComponent } from './dynamic-api.component';

const routes: Routes = [
  {
    path: '',
    component: DynamicAPIComponent,
    children: [
      {
        path: 'all-dynamic-api',
        component: AllDynamicAPIComponent,
      },
      {
        path: 'create',
        component: DynamicApiCreateComponent,
      },
      {
        path: 'edit/:id',
        component: DynamicApiUpdateComponent,
        pathMatch: 'full'
      },
      { path: '', redirectTo: '', pathMatch: 'full' },
      { path: '**', redirectTo: '', pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DynamicAPIRoutingModule { }
