import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ClipboardService } from 'ngx-clipboard';
import { DynamicAPIInterface } from 'src/app/shared/models/dynamicAPI.interface';
import { DynamicAPIService } from 'src/app/shared/service/dynamicApi.service';
import { NotificationService } from 'src/app/shared/service/notification.service';
import { UsersService } from 'src/app/shared/service/users.service';

@Component({
  selector: 'app-all-dynamic-api',
  templateUrl: './all-dynamic-api.component.html',
  styleUrls: ['./all-dynamic-api.component.scss']
})
export class AllDynamicAPIComponent implements OnInit {
  loadingIndicator = true;
  rows: Array<DynamicAPIInterface>;
  hidden = false;
  //Local Storage Company
  localStorageCompany: any;
  constructor(
    private cdr: ChangeDetectorRef,
    private router: Router,
    private userService: UsersService,
    private notification: NotificationService,
    private clipboardService: ClipboardService,
    private dynamicAPIService: DynamicAPIService
  ) { }

  ngOnInit(): void {
    this.localStorageCompany = this.getSelectedCompanyFromLocalStorage();

    //access page part
    if (!this.localStorageCompany) {
      this.hidden = true;
      this.notification.showError("Please select your Company!", "")
    } else {
      this.hidden = false;
    }

    this.getDynamicApis();
  }

  getDynamicApis() {
    this.dynamicAPIService.getDynamicAPIs().subscribe((res) => {
      this.rows = res;
      this.loadingIndicator = false;
      this.cdr.detectChanges();
    }, (err) => {
      this.notification.showError(err.error, "");
    });
  }

  handleAddDynamicAPI() {
    this.router.navigateByUrl('/dynamicAPI/create');
  }

  //Gets the Selected Company from Local Storage
  getSelectedCompanyFromLocalStorage() {
    return this.userService.getSelectedCompanyFromLocalStorage();
  }

  editDynamicAPI(dynamicAPIId: string) {
    this.router.navigateByUrl('/dynamicAPI/edit/' + dynamicAPIId);
  }

  deleteDynamicAPI(id: any) {
    if(window.confirm('Do you want to go ahead?')) {
      this.dynamicAPIService.deleteDynamicAPI(id).subscribe((data) => {
        this.getDynamicApis();
        this.notification.showSuccess('Successfully deleted Dynamic Api.', "");
      }, (error) => {
        console.log(error)
        this.notification.showError(`Error deleting deleteDynamicAPI: ${error}`, "");
      });
    } 
  }

  copyToDynamicAPIClipboard(row: any) {
    const apiId = row.urlKey;
    row.copy = true;
    let dynamicAPILink = ''
    if (row.selectedOption === 'one') {
      if (row.subid !== 'never') {
        if (row.subidKey !== 'never') {
          dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}?date=YYYY-MM-DD&subid=${row.subidKey}`;
        } else {
          dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}?date=YYYY-MM-DD&subid=SUBIDKEY`;
        }
        
      } else {
        dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}?date=YYYY-MM-DD`;
      }
      
    } else if (row.selectedOption === 'two') {
      if (row.subid !== 'never') {
        if (row.subidKey !== 'never') {
          dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}?start_date=YYYY-MM-DD&end_date=YYYY-MM-DD&subid=${row.subidKey}`;
        } else {
          dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}?start_date=YYYY-MM-DD&end_date=YYYY-MM-DD&subid=SUBIDKEY`;
        }
        
      } else {
        dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}?start_date=YYYY-MM-DD&end_date=YYYY-MM-DD`;
      }
      
    } else {
      if (row.subid !== 'never') {
        if (row.subidKey !== 'never') {
          dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}&subid=${row.subidKey}`;
        } else {
          dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}&subid=SUBIDKEY`;
        }
        
      } else {
        dynamicAPILink = `http://api.nextsys.io/api/dynamicAPI/uri/${apiId}`;
      }
      
    }
    this.clipboardService.copyFromContent(dynamicAPILink);
    setTimeout(()=>{    
      row.copy = false;
      this.cdr.detectChanges();
    }, 3000);
  }

}
