import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DynamicAPIService } from 'src/app/shared/service/dynamicApi.service';
import { NotificationService } from 'src/app/shared/service/notification.service';
import { UsersService } from 'src/app/shared/service/users.service';

@Component({
  selector: 'app-dynamic-api-create',
  templateUrl: './dynamic-api-create.component.html',
  styleUrls: ['./dynamic-api-create.component.scss']
})
export class DynamicApiCreateComponent implements OnInit {
  @ViewChild(FormGroupDirective) formGroupDirective: FormGroupDirective;
  dynamicAPIFG: FormGroup;
  hidden = false;
  localStorageCompany: any;

  options = [
    { label: 'One Date', value: 'one' },
    { label: 'Two Date', value: 'two' },
  ];
  constructor(
    private router: Router,
    private notification: NotificationService,
    private userService: UsersService,
    private dynamicAPIService: DynamicAPIService,
  ) { 
    this.localStorageCompany = this.getSelectedCompanyFromLocalStorage();
  }

  ngOnInit(): void {
    if (!this.localStorageCompany) {
      this.hidden = true;
      this.notification.showError("Please select your Company!", "")
    } else {
      this.hidden = false;
    }

    this.dynamicAPIFG = new FormGroup({
      dynamicApiUri: new FormControl('', [Validators.required, Validators.pattern(/^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/)]),
      responseFields: new FormControl(''),
      selectedOption: new FormControl(''),
      subid: new FormControl(''),
      subidKey: new FormControl({ value: '', disabled: true }),
    });
  }

  onSubidChange(event: any) {
    const subidKeyControl = this.dynamicAPIFG.get('subidKey')
    if (event.value) {
      subidKeyControl.enable();
    } else {
      subidKeyControl.disable();
    }
  }

  //Gets the Selected Company from Local Storage
  getSelectedCompanyFromLocalStorage() {
    return this.userService.getSelectedCompanyFromLocalStorage();
  }

  submitHandle() {
    this.dynamicAPIFG.markAllAsTouched();
    if (this.dynamicAPIFG.valid) {
      this.dynamicAPIService.createDynamicAPI(this.dynamicAPIFG.value).subscribe(res => {
        this.notification.showSuccess('Successfully added a new Dynamic API.', "");
        this.router.navigate(['/dynamicAPI/all-dynamic-api']);
      }, (error) => {
        this.notification.showError(JSON.stringify(error.error), "");
      })
    }
  }

}
